import React from "react";

const CountReducer = (state, action) => {
  console.log("reducer function is called");

  switch (action.type) {
    case "increament":
        if (state.count === 5) {
            return { count: 0 };
        } else {
            return { count: state.count + 1 };
        }

    case "decreament":
      return { count: state.count - 1 };
      case "add":
      return { count: state.count + action.payload };

    default:
      return state;
  }
};

export default CountReducer;
